* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  line-height: 1;
  min-width: 320px;
  position: relative;
}

article,
aside,
details,
figure,
figcaption,
footer,
header,
hgroup,
main,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}

fieldset {
  border: 0;
  padding: 0;
  margin: 0;
  min-width: 0;
}

.hidden,
[hidden],
template {
  display: none;
}

a {
  text-decoration: none;
  background: transparent;
}
a:active, a:hover {
  outline: 0;
}
a img {
  border: 0;
}

b,
strong {
  font-weight: bold;
}

small {
  font-size: 75%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

svg:not(:root) {
  overflow: hidden;
}

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  border: 0;
  font: inherit;
  color: inherit;
  appearance: none;
  border-radius: 0;
  text-transform: none;
  box-sizing: border-box;
  outline: none !important;
  background-clip: padding-box;
  background-color: transparent;
}

button,
html input[type=button] {
  cursor: pointer;
  appearance: button;
}

button[disabled],
html input[disabled] {
  cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

input {
  line-height: normal;
}
input[type=reset], input[type=submit] {
  cursor: pointer;
  appearance: button;
}
input[type=checkbox], input[type=radio] {
  box-sizing: border-box;
  padding: 0;
}
input[type=number]::-webkit-inner-spin-button, input[type=number]::-webkit-outer-spin-button {
  height: auto;
}
input[type=search] {
  appearance: textfield;
  box-sizing: border-box;
}
input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-decoration {
  appearance: none;
}

textarea {
  resize: none;
  overflow: auto;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
}

td,
th {
  padding: 0;
}

ol,
ul {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5 {
  color: #000;
  font-weight: 400;
}

img {
  max-width: 100%;
  height: auto;
  pointer-events: none;
  user-select: none;
  border: 0;
}

.clearfix {
  clear: both;
}

.noSelect {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.noSelect:focus {
  outline: none !important;
}

html {
  min-height: 100vh;
}

body {
  background-color: #FAFBFF;
  color: #292D32;
  font-size: 14px;
  font-family: "Poppins", sans-serif;
  font-weight: normal;
  font-style: normal;
  overflow-x: hidden;
  min-height: 100vh;
  line-height: 1.5;
}
body.ovf_hidden {
  overflow: hidden;
}

.container {
  max-width: 1500px;
  width: 100%;
  margin: 0 auto;
  padding: 0 25px;
}

.overflow-hidden {
  overflow: hidden !important;
}

main {
  position: relative;
}

[data-aos=swipe-horizontal] {
  width: 0;
  transition-property: width;
}
[data-aos=swipe-horizontal].aos-animate {
  width: 100%;
}

.section {
  overflow: hidden;
  border-radius: 30px;
  box-shadow: 0px 10px 60px 0px rgba(226, 236, 249, 0.5);
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  min-height: 360px;
}
.section__header {
  flex-wrap: wrap;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 38px 20px;
}
@media (max-width: 1199px) {
  .section__header {
    padding: 30px 30px 20px;
  }
}
@media (max-width: 1024px) {
  .section__header {
    padding: 15px 15px 0px;
  }
}
.section__title {
  font-size: 22px;
  font-weight: 600;
  letter-spacing: -0.22px;
  margin-bottom: 8px;
}
.section__subtitle {
  color: #16C098;
}
.section__body {
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.section__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 40px;
}
@media (max-width: 1199px) {
  .section__footer {
    padding: 30px 30px;
  }
}
@media (max-width: 1024px) {
  .section__footer {
    flex-direction: column-reverse;
    padding: 30px 15px;
  }
}
.section__footer .showing-data {
  color: #B5B7C0;
  font-weight: 500;
}
@media (max-width: 1024px) {
  .section__footer .showing-data {
    text-align: center;
  }
}
.form {
  display: flex;
}
.form input {
  width: 100%;
  background: #F9FBFF;
  height: 38px;
  border-radius: 10px;
}
.form input::placeholder {
  color: #B5B7C0;
}
.form--search {
  max-width: 216px;
  width: 100%;
}
@media (max-width: 1024px) {
  .form--search {
    margin-top: 10px;
    max-width: 100%;
  }
}
.form--search input {
  background: #F9FBFF url(../img/icon-search.svg) 8px center no-repeat;
  padding-left: 40px;
}
.form button {
  display: none;
}

.table {
  font-weight: 500;
  width: 100%;
  border-bottom: 1px solid #B5B7C0;
  min-width: 800px;
}
.table__text {
  border: 1px solid transparent;
  border-radius: 4px;
  padding: 4px;
  min-width: 80px;
  display: inline-block;
}
.table__text--active {
  color: #00B087;
  border-color: #00B087;
  background-color: rgba(0, 176, 135, 0.3);
  text-align: center;
}
.table__text--inactive {
  color: #DF0404;
  border-color: #DF0404;
  background-color: rgba(223, 4, 4, 0.3);
  text-align: center;
}
.table__container {
  height: 100%;
  overflow: auto;
  padding: 0 40px;
}
@media (max-width: 1199px) {
  .table__container {
    padding: 0 30px;
  }
}
@media (max-width: 1024px) {
  .table__container {
    padding: 0 15px;
  }
}
.table thead {
  position: sticky;
  top: 0;
  background-color: #ffffff;
}
.table thead::after {
  content: "";
  bottom: 0;
  left: -40px;
  right: -40px;
  position: absolute;
  height: 1px;
  background: #EEEEEE;
}
.table thead tr {
  color: #B5B7C0;
}
.table thead th {
  font-weight: 500;
  text-align: left;
  height: 49px;
}
.table tbody tr {
  border-bottom: 1px solid #eeeeee;
}
.table tbody td {
  height: 69px;
}
@media (max-width: 1024px) {
  .table tbody td {
    height: 59px;
  }
}
.pagination {
  font-size: 12px;
  font-weight: 500;
}
@media (max-width: 1024px) {
  .pagination {
    margin-bottom: 15px;
  }
}
.pagination__list {
  display: flex;
}
.pagination__list-el {
  display: flex;
  margin-right: 12px;
}
@media (max-width: 767px) {
  .pagination__list-el:nth-of-type(5) {
    display: none;
  }
}
.pagination__list-el:last-of-type {
  margin-right: 0;
}
.pagination__list-link {
  align-items: center;
  justify-content: center;
  text-align: center;
  min-width: 25px;
  height: 24px;
  padding: 0 5px;
  border-radius: 4px;
  display: flex;
  color: #292D32;
  border: 1px solid #EEE;
  background: #F5F5F5;
}
.pagination__list-link:hover, .pagination__list-link.active {
  background-color: #5932EA;
  color: #ffffff;
  border-color: #5932EA;
}

.header {
  background: #ffffff;
  max-width: 306px;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  box-shadow: 0px 10px 60px 0px rgba(226, 236, 249, 0.5);
  z-index: 2;
  padding: 37px 28px 75px;
  overflow: auto;
  display: flex;
  flex-direction: column;
}
@media (max-width: 1024px) {
  .header {
    max-width: 100%;
    bottom: auto;
    right: 0;
    padding: 0;
  }
}
.header__header {
  margin-bottom: 65px;
  background-color: #ffffff;
}
@media (max-width: 1024px) {
  .header__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    margin-bottom: 0;
    z-index: 3;
    padding: 15px;
    min-height: 65px;
  }
}
.header__logo {
  display: flex;
}
@media (max-width: 1024px) {
  .header__logo {
    width: auto;
    height: 32px;
  }
}
.header__toggle {
  display: none;
  width: 35px;
  height: 22px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1024px) {
  .header__toggle {
    display: flex;
  }
}
.header__toggle:hover span {
  background-color: #5932EA;
}
.header__toggle span {
  height: 3px;
  width: 100%;
  background: #292D32;
  border-radius: 10px;
}
.header__body {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  z-index: 2;
}
@media (max-width: 1024px) {
  .header__body {
    box-shadow: 0px 10px 60px 0px rgba(226, 236, 249, 0.5);
    transform: translateX(-100%);
    transition: all 0.4s;
  }
}
@media (max-width: 1024px) {
  .header__body {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    padding: 90px 15px 30px;
    background-color: #ffffff;
    max-width: 306px;
    overflow: auto;
  }
}
.header__menu {
  margin-bottom: 30px;
}
.header__menu-el {
  display: flex;
  margin-bottom: 18px;
}
.header__menu-el:last-of-type {
  margin-bottom: 0;
}
.header__menu-el:hover .header__menu-icon, .header__menu-el.active .header__menu-icon {
  background-position-x: 24px;
}
.header__menu-el:hover .header__menu-link, .header__menu-el.active .header__menu-link {
  color: #ffffff;
  background: #5932EA;
}
.header__menu-el:hover .header__menu-link::after, .header__menu-el.active .header__menu-link::after {
  background-image: url(../img/chevron-right-white.svg);
}
.header__menu-link {
  padding: 11px;
  display: flex;
  align-items: center;
  border-radius: 8px;
  width: 100%;
  color: #9197B3;
}
.header__menu-link::after {
  content: "";
  background: url(../img/chevron-right-grey.svg) center no-repeat;
  width: 16px;
  height: 16px;
  flex-shrink: 0;
}
.header__menu-icon {
  width: 24px;
  height: 24px;
  background-image: url(../img/menu-icons-sprite.svg);
  flex-shrink: 0;
  margin-right: 14px;
  background-position-x: 0;
}
.header__menu-icon--dashboard {
  background-position-y: 0px;
}
.header__menu-icon--product {
  background-position-y: -24px;
}
.header__menu-icon--customers {
  background-position-y: -48px;
}
.header__menu-icon--income {
  background-position-y: -72px;
}
.header__menu-icon--promote {
  background-position-y: -96px;
}
.header__menu-icon--help {
  background-position-y: -120px;
}
.header__menu-link-text {
  width: 100%;
}
.header .user-element {
  display: flex;
  align-items: center;
}
.header .user-element:hover .user-element__name {
  color: #5932EA;
}
.header .user-element__avatar-container {
  border-radius: 100px;
  width: 42px;
  height: 42px;
  flex-shrink: 0;
  margin-right: 12px;
  overflow: hidden;
}
.header .user-element__avatar {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.header .user-element__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.header .user-element__name {
  font-weight: 500;
  font-size: 14px;
  color: #292D32;
  line-height: 1;
}
.header .user-element__post {
  font-size: 14px;
  color: #757575;
}
.header__bg {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.4);
  z-index: 1;
  display: none;
}
@media (max-width: 1024px) {
  .header__bg {
    display: flex;
    transition: all 0.4s;
    transform: translateX(100%);
  }
}
.header.active .header__toggle {
  height: 34px;
  position: relative;
  transform: rotate(45deg);
}
.header.active .header__toggle span {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
}
.header.active .header__toggle span:nth-of-type(2) {
  width: 3px;
  height: 100%;
}
.header.active .header__toggle span:nth-of-type(3) {
  display: none;
}
.header.active .header__body {
  transform: translateX(0deg);
}

.header__bg.active {
  transform: translateX(0%);
}

.content {
  margin-left: 306px;
  padding: 41px 95px 41px 71px;
  height: 100vh;
  display: flex;
  flex-direction: column;
}
@media (max-width: 1199px) {
  .content {
    padding: 30px;
  }
}
@media (max-width: 1024px) {
  .content {
    margin-left: 0;
    padding: 15px;
  }
}
.content__title {
  margin-bottom: 51px;
  font-size: 24px;
  font-weight: 500;
}
@media (max-width: 1199px) {
  .content__title {
    margin-bottom: 20px;
  }
}